.contact {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #013741;
  margin-bottom: 50px; }

.test {
  background-color: #013741; }

.contact__information {
  background-color: #014550;
  background-image: url("/img/top-img.png");
  background-repeat: no-repeat;
  background-position: 100% 100%;
  display: flex; }

.information__inner {
  max-width: 820px;
  padding-top: 50px;
  padding-bottom: 50px; }

.contact__form {
  background-color: #013741; }

.form__inner {
  max-width: 1120px;
  padding-top: 50px;
  padding-bottom: 50px; }

.partnership {
  margin-bottom: 50px; }

.partnership__inner {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column; }

.partnership__header {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 40px;
  line-height: 48px;
  text-align: center; }

.partnership__programm {
  display: flex;
  justify-content: space-between; }

.partnership__information {
  padding-left: 10px;
  max-width: 740px; }

.video__body {
  border-radius: 20px;
  width: 300px;
  height: 174px; }

.video__text {
  margin-bottom: 0;
  margin-top: 10px;
  color: #b7b7b7;
  font-weight: 700; }

.partnership__text,
.partnership__benefit,
.partnership__list {
  margin-top: 0; }

.partnership__text {
  margin-bottom: 20px; }

.partnership__benefit {
  margin-bottom: 0; }

.partnership__list {
  margin-bottom: 25px;
  padding-left: 17px; }

.partnership__item {
  margin-bottom: 10px; }

.partnership__access {
  display: flex;
  justify-content: space-between; }

.partnership__details {
  margin-bottom: 15px; }

.experts {
  margin-bottom: 50px; }

.experts__inner {
  display: flex;
  flex-direction: column; }

.experts__header {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 40px;
  line-height: 48px; }

.experts__list {
  margin-top: 0;
  list-style: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
  grid-column-gap: 20px; }

.experts__item {
  position: relative;
  max-width: 210px; }

.experts__item::before {
  content: "";
  position: absolute;
  background-image: url("/img/dots-white.png");
  background-repeat: no-repeat;
  width: 130px;
  height: 164px;
  top: -20px;
  right: 3px;
  background-color: transparent; }

.experts__item--active .experts__image {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4); }

.experts__image {
  position: relative;
  border-radius: 50px;
  width: 164px;
  height: 158px; }

.experts__title {
  margin-top: 10px;
  margin-bottom: 5px;
  font-size: 32px;
  line-height: 40px; }

.experts__text {
  margin-bottom: 0;
  margin-top: 0;
  color: #b7b7b7; }

.footer {
  align-self: flex-end;
  background-color: #f1f1f1; }

.footer__inner {
  display: flex;
  justify-content: space-between; }

.footer__links,
.footer__social,
.footer__list,
.social__list {
  display: flex; }

.footer__list,
.social__list {
  list-style: none;
  padding-left: 0;
  margin-top: 0; }

.modal__inner,
.modal-success__inner {
  display: flex;
  width: 460px;
  min-height: 605px;
  position: fixed;
  top: calc(50% - 295px);
  right: 0;
  left: 0;
  bottom: auto;
  margin: auto;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  z-index: 2;
  background-color: #ffffff; }

.modal,
.modal-success {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.8);
  font-family: "Rajdhani", "Arial", sans-serif; }

.modal__data {
  display: flex;
  flex-direction: column;
  max-width: 490px;
  padding: 33px 40px 36px; }

.modal__header,
.modal__text {
  margin-top: 0;
  color: #f1f1f1;
  text-align: left; }

.modal__header {
  margin-bottom: 20px;
  font-size: 28px;
  line-height: 39px; }

.modal__text {
  margin-bottom: 51px;
  font-size: 16px;
  line-height: 22px;
  max-width: 380px; }

.modal__input,
.modal__textarea {
  padding: 16px;
  margin-bottom: 30px;
  min-width: 380px;
  background-color: #e1e6ed;
  border: 0;
  color: #f1f1f1;
  font-size: 16px;
  line-height: 24px; }

.modal__form textarea {
  resize: none; }

.modal__form button {
  width: 100%;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #ffffff;
  background-color: #192b3f;
  font-size: 24px;
  line-height: 32px;
  border: 0;
  cursor: pointer;
  margin-bottom: 15px; }

.modal__button:hover,
.modal__button:focus {
  background-color: #2e4055; }

.modal__button:active {
  background-color: #3b536d; }

.modal-success__title {
  margin: 0 auto;
  display: flex;
  align-items: center;
  max-width: 380px; }

.modal-success__text {
  color: #f1f1f1;
  font-size: 24px;
  line-height: 31px; }

.modal__close,
.modal-success__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 26px;
  height: 28px;
  border: none;
  background-color: inherit;
  cursor: pointer;
  color: #000000; }

.modal__checkbox-label {
  max-width: 380px;
  display: flex;
  position: relative;
  min-height: 16px;
  cursor: pointer;
  color: #f1f1f1;
  font-size: 14px;
  line-height: 16px;
  padding-left: 30px;
  align-items: center;
  text-align: left; }

.modal__checkbox {
  position: absolute;
  appearance: none;
  display: none; }

.modal__checkbox-text {
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0;
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat; }

.modal__checkbox:checked + .modal__checkbox-text {
  background-image: url("../img/checkbox-checked.svg");
  background-repeat: no-repeat; }

.modal__lowerpart {
  display: flex;
  flex-direction: column-reverse; }

.hidden {
  display: none; }

@media (max-width: 1023px) {
  .modal__inner,
  .modal-success__inner {
    justify-content: center; }
  .modal__data {
    width: 300px;
    padding: 10px; }
  .modal__text {
    margin-bottom: 51px;
    font-size: 16px;
    line-height: 22px;
    max-width: 320px; }
  .modal__form input,
  .modal__form textarea {
    min-width: 280px; } }

@media (max-width: 719px) {
  .modal__inner,
  .modal-success__inner {
    width: 100%; } }
