.footer {
  align-self: flex-end;
  background-color: $grey;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
}

.footer__links,
.footer__social,
.footer__list,
.social__list {
  display: flex;
}

.footer__list,
.social__list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
}
